<template>
  <div class="setting-section" v-if="show">
    <div class="setting-title">
      <span></span>
    </div>
    <div class="setting-content">
      <div v-for="list in lists" :key="list.id">
        <h3 class="title">{{ list.name }}
          <a :href="baseUrl + list.link" target="_blanck" class="location">
            <i class="el-icon-location-outline info"></i>
            <span class="info"> </span>
          </a>
        </h3>
        <el-row class="pt-md">
          <el-col :xs="24" :sm="12" :md="8" :lg="6" v-for="item in list.children" :key="item.id" style="padding: 0 10px;">
            <el-card :body-style="{ padding: '0px' }">
              <img class="img" :src="baseUrl + item.url">
              <div class="content flexbox-between">
                <span class="fb">{{ item.label }}</span>
                <el-button type="text" size="medium" class="button p-none" @click="openUploadDialog(item.url, item.tip)"></el-button>
              </div>
              <div class="mask" @click="open(item.url)">
                <span class="mask-tip"></span>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </div>
      <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="480px">
        <el-upload
          drag
          ref="upload"
          action="string"
          :auto-upload="false"
          :http-request="uploadFunc"
          class="upload">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text"><em></em></div>
          <div class="el-upload__tip" slot="tip">
            <span style="color: red; line-height: 2;">* {{ tips }}</span><br>
            <span style="color: red; line-height: 2;">*  () *</span>
          </div>
        </el-upload>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="upload"> </el-button>
        </span>
      </el-dialog>
    </div>
    <div class="viewImg" ref="dialog">
      <img :src="baseUrl + currentImgUrl" alt="">
      <div class="close" @click="close"></div>
    </div>
  </div>
</template>
<script>
import UploadService from '@/services/others';
import CONFIG from '@/config';

export default {
  name: 'customization',
  data() {
    return {
      dialogVisible: false,
      tips: '',
      baseUrl: 'http://htcc.org.cn',
      show: true,
      lists: [
        {
          id: 1,
          name: '',
          link: '',
          children: [
            { id: 11, url: '/static/images/bg.png', label: '', tip: '1920 * 1080' },
            { id: 12, url: '/static/images/logo.png', label: 'Logo', tip: '240 * 240' },
            { id: 13, url: '/static/images/logo-en.png', label: 'Logo - ', tip: '240 * 240' },
          ],
        },
        { 
          id: 2,
          name: '',
          link: '/page/telem-colla.html',
          children: [
            { id: 21, url: '/static/images/s-bg.png', label: '', tip: '1920 * 1080' },
            { id: 22, url: '/static/images/s-logo-en.png', label: 'Logo', tip: '350 * 60' },
            { id: 22, url: '/static/images/s-logo-en.png', label: 'Logo - ', tip: '350 * 60' },
          ],
        },
        {
          id: 3,
          name: '',
          link: '/page/main.html',
          children: [
            { id: 31, url: '/static/images/main-logo.png', label: 'Logo', tip: '270 * 70' },
            { id: 31, url: '/static/images/main-logo-en.png', label: 'Logo - ', tip: '270 * 70' },
            { id: 32, url: '/static/images/lib.png', label: '', tip: '1920 * 160' },
            { id: 33, url: '/static/images/info.png', label: '', tip: '570 * 445' },
          ],
        },
        {
          id: 4,
          name: '',
          link: '/page/about/overview.html',
          children: [
            { id: 41, url: '/static/images/sub/about-bg.png', label: 'Header ', tip: '1920 * 240' },
          ],
        },
        {
          id: 5,
          name: '',
          link: '/page/news/notice.html',
          children: [
            { id: 51, url: '/static/images/sub/news-bg.png', label: 'Header ', tip: '1920 * 240' },
          ],
        },
        {
          id: 6,
          name: '',
          link: '/page/research/orgs.html',
          children: [
            { id: 61, url: '/static/images/ky/bg.png', label: 'Header ', tip: '1920 * 240' },
            { id: 62, url: '/static/images/ky/org-1.png', label: ' - ', tip: '1000 * 280' },
            { id: 63, url: '/static/images/ky/org-2.png', label: ' - ', tip: '1000 * 280' },
            { id: 64, url: '/static/images/ky/team-1.png', label: ' - ', tip: '420 * 280' },
            { id: 65, url: '/static/images/ky/team-2.png', label: ' - ', tip: '420 * 280' },
            { id: 66, url: '/static/images/ky/team-3.png', label: ' - ', tip: '420 * 280' },
            { id: 67, url: '/static/images/ky/team-4.png', label: ' - ', tip: '420 * 280' },
            { id: 68, url: '/static/images/ky/rs-1.png', label: ' - ', tip: '1000 * 280' },
            { id: 69, url: '/static/images/ky/rs-2.png', label: ' - ', tip: '1000 * 280' },
          ],
        },
        {
          id: 7,
          name: '',
          link: '/page/join/condition.html',
          children: [
            { id: 71, url: '/static/images/sub/join-bg.png', label: 'Header ', tip: '1920 * 240' },
          ],
        },
      ],
      currentImgUrl: '',
      currentPath: '',
    };
  },
  methods: {
    uploadFunc(data) {
      const isLt10M = data.file.size / 1024 / 1024 < 10;
      if (!isLt10M) {
        this.$message.error('(10M)!');
        this.$refs.upload.clearFiles();
        return;
      }
      const file = data.file;
      const path = this.currentPath;
      const params = new FormData();
      params.append('path', path);
      params.append('picture', file);

      UploadService.importImage(params)
        .then((res) => {
          if (res.data.status === '200') {
            this.$message.success(res.data.msg);
            this.dialogVisible = false;
            this.show = false;
            this.show = true;
          } else {
            this.$message.warning(res.data.msg);
            this.$refs.upload.clearFiles();
          }
        });
    },
    openUploadDialog(path, tip) {
      this.tips = tip;
      this.currentPath = path;
      this.dialogVisible = true;
    },
    upload() {
      this.$refs.upload.submit();
    },
    open(url) {
      this.currentImgUrl = url;
      this.$refs.dialog.style.display = 'block';
    },
    close() {
      this.currentImgUrl = '';
      this.$refs.dialog.style.display = 'none';
    },
  },
  created() {},
};
</script>
<style lang="scss" scoped>
.viewImg {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .7);
  display: none;
  &>img {
    position: absolute;
    left: 50%;
    top: 50%;
    max-width: 80%;
    display: block;
    transform: translate(-50%, -50%);
  }
  .close {
    position: absolute;
    right: 20px;
    top: 20px;
    width: 40px;
    height: 40px;
    text-align: center;
    border-radius: 40px;
    background-color: #666;
    cursor: pointer;
    &:after {
      content: "x";
      font-size: 36px;
      font-weight: normal;
      font-weight: 100;
      line-height: 36px;
      color: #fff;
    }
    &:hover {
      background-color: #555;
    }
  }
}
.setting-content {
  margin-top: 10px;
}
.title {
  margin-left: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #f1f2f7;
  .location {
    font-size: 14px;
    display: none;
    text-decoration: none;
    &>span {
      font-size: 13px;
      font-weight: normal;
    }
  }
  &:hover {
    .location {
      display: inline;
    }
  }
}
.el-row {
  .el-card {
    position: relative;
    height: 220px;
    margin-bottom: 20px;
    background-color: #ccc;
    .img {
      position: absolute;
      left: 50%;
      width: auto;
      height: 220px;
      transform: translateX(-50%);
      display: block;
      cursor: pointer;
    }
    .content {
      position: absolute;
      bottom: 0;
      padding: 10px 10px;
      width: 100%;
      background: rgba(255, 255, 255, .9);
    }
    .mask {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 36px;
      left: 0;
      background: rgba(0, 0, 0, .5);
      cursor: pointer;
      display: none;
      .mask-tip {
        font-size: 18px;
        color: #fff;
        display: block;
        margin: 0 auto;
        margin-top: 100px;
        text-align: center;
      }
    }
    &:hover {
      .mask {
        display: block;
      }
    }
  }
}
.upload {
  margin: 0 auto;
  width: 360px;
}
</style>



