import $http from '@/services/http';
import qs from 'qs';

// 
const getList = (pageSize, pageNum, type) => $http.get('/news/findAllByType', {
  params: {
    pageSize,
    pageNum,
    type,
  },
});

// 
const getContent = id => $http.get('/news/getNewsById', {
  params: { id },
});

// 
const create = (title, type, tabloid, content, attachments) => $http.post('/news/insertNews', {
  title,
  type,
  tabloid,
  content,
  attachments,
});

// 
const edit = (id, title, type, tabloid, content, attachments) => $http.post('/news/updateNews', {
  id,
  title,
  type,
  tabloid,
  content,
  attachments,
});
// 
const editTime = (id, publishDate, updateDate) => $http.post(`/news/updateNewsTime?id=${id}&publishDate=${publishDate}&updateDate=${updateDate}`);

// 
const deleteNews = groupId => $http.post('/news/deleteNews', { groupId });

// 
const importFile = file => $http.post('/attachment/asyncUploadFile', file);

//  Banner 
const importImage = file => $http.post('/news/uploadBanner', file);

//  Banner 
const removeBanner = id => $http.post('/news/removeBanner', qs.stringify({ id }));

// 
const setHots = id => $http.get('/news/setHotNews', {
  params: { id },
});

export default {
  getList,
  getContent,
  create,
  edit,
  editTime,
  deleteNews,
  setHots,
  importFile,
  importImage,
  removeBanner,
};



