import $http from '@/services/http';

const getImgAddress = (imgSrc) => $http.post('/news/uploadImageByBase64', {
  imgSrc
});

const importImage = file => $http.post('/resource/replacePicture', file);

// ----  ----
const getList = () => $http.get('/resource/findStaticArticle');

// ----  ----
//  ID 
const getLinkById = id => $http.get(`/resource/getFriendLinkById?id=${id}`);
// 
const getLinks = () => $http.get('/resource/findFriendLink', {
  headers: {
    'Content-Language': 'cn',
  },
});
// 
const addLink = (name, nameEn, url) => $http.post('/resource/insertFriendLink', {
  name, nameEn, url,
});
// 
const editLink = (id, name, nameEn, url) => $http.post('/resource/updateFriendLink', {
  id, name, nameEn, url,
});
// 
const deleteLink = id => $http.post(`/resource/deleteFriendLink?id=${id}`);
// 
const getAddress = () => $http.get('/hospital/findHospitalAddressByName?addressName=');

export default {
  getImgAddress,
  importImage,
  getList,
  getLinkById,
  getLinks,
  addLink,
  editLink,
  deleteLink,
  getAddress,
};


